import { useShallow } from "zustand/react/shallow";
import { useDataSourceStore } from "./store";
import { Alert, Box, Button, Typography } from "@mui/material";
import React from "react";
import { restApiSchema } from "./variant/restAPI";
import widgets from "../propsPanel/widgets";
import { map } from "lodash-es";
import { useCallback } from "react";
import { useState } from "react";
import ReactCodeMirror, { EditorView } from "@uiw/react-codemirror";
import { safeStringifyJSON } from "../misc";
import { useLayoutEffect } from "react";
import { commonTheme } from "../codemirror";
import { useMemo } from "react";
import { DataSourceTypeEnum } from "../type";
import Select from "../builtIn/components/Select";

const uiSchemas = {
  restApi: restApiSchema,
};

const QueryConfigItem = props => {

  const Widget = widgets[props.widget];
  const {
    widgetProps,
    queryId,
    field,
  } = props;

  const value = useDataSourceStore(state => state.queries[queryId]?.options[field]);

  const handleChange = useCallback(val => {
    useDataSourceStore.getState().setQueryOptions(queryId, { [field]: val });
  }, [queryId, field]);

  return (
    <Box>
      <Typography
        fontSize={12}
      >
        {props.name}
      </Typography>
      <Box width={'203px'}>
        <Widget {...widgetProps} value={value} onChange={handleChange} />
      </Box>
    </Box>
  )
}

const ExecResultDisplay = (props) => {
  const { data, error } = props;

  const stringifyData = safeStringifyJSON(data, null, 2);

  return (
    <Box>
      {(data || error) && <Typography
        fontSize={13}
        fontWeight={800}
        lineHeight={1.5}
      >
        执行结果
      </Typography>}
      {data && <Box height={'160px'}>
        <ReactCodeMirror
          height="100%"
          value={stringifyData}
          readOnly
          extensions={[
            EditorView.lineWrapping,
            commonTheme,
          ]}
        />
      </Box>}
      {error && <Alert severity="error">
        {error}
      </Alert>}
    </Box>
  )
}

export const QueryConfigPanel = () => {
  const { selectedQueryId } = useDataSourceStore(useShallow(state => ({
    selectedQueryId: state.selectedQueryId,
    queries: state.queries,
  })));

  const selectedQuery = useDataSourceStore(state => state.queries[selectedQueryId]);
  const selectedQueryFn = useDataSourceStore(state => state.queryHandlers[selectedQueryId]);

  const selectedUiSchema = uiSchemas[selectedQuery?.type] || {};

  const conditions = selectedUiSchema.conditions;
  const disableExec = useMemo(() => {
    if (!selectedQueryFn) {
      return true;
    }
    if (!conditions?.enabled) {
      return false;
    }
    return !conditions.enabled(selectedQuery);
  }, [conditions, selectedQuery, selectedQueryFn])
  const [isRunning, setIsRunning] = useState(false);

  const [result, setResult] = useState(null);
  const [error, setError] = useState('');

  useLayoutEffect(() => {
    setResult(null);
    setError('');
  }, [selectedQueryId, selectedQuery?.type]);

  const handleExec = useCallback(async () => {
    if (isRunning) {
      return;
    }
    setIsRunning(true);
    try {
      const queryHandlers = useDataSourceStore.getState().queryHandlers;;
      const specHandler = queryHandlers[selectedQueryId];
      if (!specHandler) {
        return;
      }
      try {
        const res = specHandler()
        const result = res instanceof Promise ? (await res) : res;
        setError('');
        setResult(result);
        console.log('result', result);
      } catch (err) {
        setError(String(err));
        setResult(null);
        console.error('exec query error', err);
      }
    } finally {
      setIsRunning(false);
    }
  }, [selectedQueryId, isRunning]);

  const dataSourceTypeOptions = useMemo(() => {
    return Object.entries(DataSourceTypeEnum).map(([name, value]) => {
      return {
        value,
        label: name,
      }
    })
  }, [])

  return (
    <Box>
      <Box
        sx={{
          mb: '5px'
        }}
      >
        {selectedQueryId && <Box>
          <Typography
            fontSize={12}
          >
            数据源类型
          </Typography>
          <Box>
            <Select
                value={selectedQuery?.type}
                onChange={(val) => {
                  useDataSourceStore.getState().updateQueryMeta(selectedQueryId, { type: val })
                }}
                options={dataSourceTypeOptions}
             />
          </Box>
        </Box>}
      </Box>
      <Box
        display={'flex'}
        rowGap={'5px'}
        flexDirection={'column'}
      >
        {map(selectedUiSchema?.attributes, attribute => {
          return (
            <QueryConfigItem key={attribute.field} queryId={selectedQueryId} {...attribute} />
          )
        })}
      </Box>
      <Box
        display={'flex'}
        flexDirection={'column'}
        sx={{
          paddingTop: 2,
          paddingBottom: 1,
        }}
      >
        {selectedQueryId && <Button
          variant="contained"
          size="small"
          onClick={handleExec}
          sx={{ alignSelf: 'flex-end' }}
          loading={isRunning}
          disabled={disableExec}
        >
          执行
        </Button>}
      </Box>
      <ExecResultDisplay
        data={result}
        error={error}
      />
    </Box>
  )
};