import { Alert, Box, Link, Switch, Typography } from "@mui/material"
import React from "react"
import { useEffect } from "react";
import { useState } from "react"
import FunctionSandBox from "../sandbox/function";
import { generateSafeContext } from "../misc";
import { Button } from "@mui/material";
import { processComponentInfo } from "../components";
import useComponentStore from "../components/store";
import { useShallow } from "zustand/react/shallow";
import { ComponentCard } from "../componentsPanel";
import { useCallback } from "react";
import useGlobalStore from "../globalStore";
import { DEFAULT_LOCAL_DEV_PORT, NOOP } from "../constant";
import { useSchemaStore } from "../schema";
import { useTransition } from "react";
import { withErrorBoundary, withShadowDom } from "../components/wrappers";
import { reverse, uniqBy } from "lodash-es";
import ReactDOM from 'react-dom';
import { TextField } from "@mui/material";


const sandbox = new FunctionSandBox({});

export async function getCodeFromLocal(port: number = 9001) {
  try {
    const url = `http://localhost:${port}/js/main.js`;
    const code = await fetch(url, { 
      headers: {
        origin: window.origin,
      }
     }).then(res => res.text());
  
     const iframe = document.createElement('iframe');
     iframe.style.display = 'none';
     document.body.append(iframe);
  
     const sandboxWindow = iframe.contentWindow;
  
     const unusedSelf = {
        addEventListener: sandboxWindow!.addEventListener.bind(sandboxWindow),
        removeEventListener: sandboxWindow!.addEventListener.bind(sandboxWindow),
        location,
        postMessage: sandboxWindow!.postMessage.bind(sandboxWindow),
        cc: NOOP as any,
        react: React,
        'react-dom': ReactDOM,
        ilowLib: {
          Button,
          // TextField,
        },
     };
  
     const sandBoxContext = generateSafeContext({
      self: unusedSelf,
      window: sandboxWindow,
      // ...sandboxWindow,
      // window: {
      //   addEventListener: window.addEventListener,
      //   removeEventListener: window.removeEventListener,
      // }
     });
  
     sandbox.init(sandBoxContext);
     sandbox.exec(code);  
     iframe.remove();
  
    //  console.log('hee', unusedSelf.cc.default);
     const CC = unusedSelf.cc.default;
     const shadowCC = withShadowDom(CC);
     const ccInfo = {
        Component: shadowCC,
        RawComponent: withErrorBoundary(shadowCC),
        type: CC.schema.type,
        schema: (CC as any).schema,
        mark: 'debug',
     }

     window.__debugStyleElements__ = uniqBy(reverse(window.__debugStyleElements__), 'dataset.ctype').reverse();
  
     const processedInfo = processComponentInfo(ccInfo);
     useComponentStore.getState().updateComponent([ccInfo.type], processedInfo);
     return {}
  } catch (err) {
    console.error(err);
    return { err: String(err) };
  }
}

const LocalDebugger = () => {
  
  const isDebugging = useGlobalStore(state => state.isDebugging);
  const [err, setErr] = useState('');
  const [port, setPort] = useState(DEFAULT_LOCAL_DEV_PORT);

  const [isPengding, startTransition] = useTransition();

  const localAddr = `http://localhost:${port}`;

  const setErrWithTransition = useCallback((err) => {
    startTransition(() => {
      setErr(err);
    })
  }, []);

  useEffect(() => {
    if (!isDebugging) {
      return;
    }
    const socket = new WebSocket('ws://localhost:9001/ws');
    socket.addEventListener('message', (event) => {
      const message = JSON.parse(event.data);
      console.log('=========', message);
      if (message.type === 'hash') {
        console.log('新的编译hash:', message.data);
      } else if (message.type === 'ok') {
        console.log('编译成功，准备热更新');
        getCodeFromLocal().then(({ err }) => {
          if (err) {
            setErrWithTransition(err);
            return;
          }
          setErrWithTransition('');
          const { plainSchema }= useSchemaStore.getState();
          // 刷新画布
          useSchemaStore.setState({ plainSchema: { ...plainSchema } });
        });
      }
    });
    return () => {
      socket.close();
    }
  }, [isDebugging]);

  const handleChange = useCallback(async (_, checked) => {
    if (checked) {
      const { err } = await getCodeFromLocal(9001);
      if (err) {
        setErrWithTransition(err);
        return;
      }
      setErrWithTransition('');
    } else {
      useComponentStore.getState().clearDebug();
    }
    useGlobalStore.setState({ isDebugging: checked });
  }, [])

  const debugComponentinfos = useComponentStore(useShallow( state => Object.values(state).filter((c: any) => {
    return typeof c !== 'function' && c.mark === 'debug'
  })));

  return (
    <Box>
      <Typography padding={'20'} fontSize={11}>本地开发流程：<Link sx={{ cursor: 'pointer' }} fontSize={11} onClick={() => {
        window.open('https://www.npmjs.com/package/ilow-cli', '_blank');
      }}>https://www.npmjs.com/package/ilow-cli</Link></Typography>
      <Box
        padding={2}
      >
        <Typography fontSize={12} variant="caption">调试模式</Typography>
        <Switch
          onChange={handleChange}
          checked={isDebugging}
          size="small"
        />
        {err && <Alert severity="error">
          {`${err} \r\n请检查你的本地开发服务器(${localAddr})`}
        </Alert>}
        <Box>
          {debugComponentinfos.map((info: any) => {
            return (
              <ComponentCard componentInfo={info} key={info.type}  />
            )
          })}
        </Box>
      </Box>
    </Box>
  )
}

export default LocalDebugger;