import { DataSourceSchema, DataSourceTypeEnum } from "../../type";

export const restApiSchema: DataSourceSchema = {
  type: DataSourceTypeEnum.REST接口,
  attributes: [
    {
      field: 'method',
      name: '方法',
      widget: 'select',
      widgetProps: {
        options: [
          {
            value: 'get',
            label: 'GET',
          },
          {
            value: 'post',
            label: 'POST',
          },
          {
            value: 'put',
            label: 'PUT',
          },
          {
            value: 'delete',
            label: 'DELETE',
          },
          {
            value: 'option',
            label: 'OPTION',
          }
        ],
      }
    },
    {
      field: 'url',
      name: 'URL',
      widget: 'editor',
      widgetProps: {
        lineWrapping: false,
      }
    },
    {
      field: 'defaultRun',
      name: '自动执行',
      widget: 'switch',
    }
  ],
  conditions: {
    enabled: (query) => {
      return query.options.method && query.options.url?.trim();
    }
  },
}
