import { useShallow } from "zustand/react/shallow"
import { useDataSourceStore } from "./store"
import { Box, Button, Divider, IconButton, List, ListItem, ListItemButton, ListItemIcon, ListItemText, Menu, MenuItem, Typography } from "@mui/material";
import React from "react";
import { isProd } from "../misc";
import { Construction } from "@mui/icons-material";
import { QueryConfigPanel } from "./ui";
import { Add } from "@mui/icons-material";
import { useCallback } from "react";
import { debounce, isEmpty } from "lodash-es";
import Tooltip from "../builtIn/components/Tooltip";
import { MoreHorizOutlined } from "@mui/icons-material";
import { useState } from "react";

export const DataSourceEntry = () => {
  const { queries, selectedQueryId } = useDataSourceStore(useShallow(state => ({
    queries: state.queries,
    selectedQueryId: state.selectedQueryId,
  })));

  const onAdd = useCallback(debounce(() => {
    const newQueryId = useDataSourceStore.getState().addQuery();
    useDataSourceStore.setState({ selectedQueryId: newQueryId });
  }, 100), []);

  const onSelected = useCallback((id) => {
    useDataSourceStore.setState({ selectedQueryId: id });
  }, []);

  const [queryMenu, setQueryMenu] = useState<any>('');
  const [menuEl, setMenuEl] = useState<any>(null);
  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column'
      }}
      onKeyDown={e => e.stopPropagation()}
    >
      <Tooltip
        title='新增查询'
        placement="bottom-start"
      >
        <IconButton
          onClick={onAdd}
          size="small"
          sx={{
            fontSize: '18px',
            alignSelf: 'flex-end',
            position: 'relative',
            top: '5px',
            right: '4px',
          }}
        >
          <Add fontSize="inherit" />
        </IconButton>
      </Tooltip>
      <Box
        sx={{
          padding: 2,
          paddingTop: '2px'
        }}
      >
        <List
          sx={{
            height: '30vh',
            overflow: 'auto',
            border: '1px solid var(--border-1)',
            borderRadius: '5px',
            padding: 0,
            position: 'relative'
          }}
          >
          {isEmpty(queries) && (
            <Box
              height={'100%'}
              sx={{
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
              }}
            >
              <Button
                onClick={onAdd}
                size="small"
                sx={{
                  fontSize: 12
                }}
                variant="outlined"
              >新建查询</Button>
            </Box>
          )}
          {Object.values(queries).map((query: any) => {
            return (
              <ListItem
                disablePadding
                onClick={() => onSelected(query.id)}
                key={query.id}
              >
                <ListItemButton
                  sx={{
                    height: 30,
                    display: 'flex',
                    '&:hover': {
                      bgcolor: 'var(--container-bg-5)',
                    },
                    bgcolor: query.id === selectedQueryId ? 'var(--container-bg-hover-1) !important' : '',
                  }}
                >
                  <ListItemText>
                    <Typography 
                      fontSize={12}
                      width={'50px'}
                      overflow={'hidden'} 
                      whiteSpace={'nowrap'} 
                      textOverflow={'ellipsis'}
                    >
                      {query.id}
                    </Typography>
                  </ListItemText>
                  <ListItemText>
                    <Typography 
                      fontSize={12} 
                      color="textSecondary"
                      overflow={'hidden'} 
                      whiteSpace={'nowrap'} 
                      textOverflow={'ellipsis'}
                    >
                      {query.name}
                    </Typography>
                  </ListItemText>
                  <IconButton
                    size="small"
                    sx={{
                      fontSize: 12
                    }}
                    onClick={e => {
                      e.stopPropagation();
                      e.preventDefault();
                      setQueryMenu(query.id);
                      setMenuEl(e.currentTarget!);
                    }}
                  >
                    <MoreHorizOutlined
                      fontSize="inherit" 
                    />
                  </IconButton>
                </ListItemButton>
              </ListItem>
            )
          })}
        </List>
      </Box>
      <Divider />
      <Box
        sx={{
          padding: 2
        }}
      >
        <QueryConfigPanel />
      </Box>
      <Menu
        open={!!queryMenu}
        anchorEl={menuEl}
        onClose={() => setQueryMenu('')}
      >
        <MenuItem
         sx={{ color: 'red', fontSize: 11 }}
         onClick={() => {
          setQueryMenu('');
          useDataSourceStore.getState().deleteQuery(queryMenu);
          if (selectedQueryId === queryMenu) {
            useDataSourceStore.setState({ selectedQueryId: '' });
          }
         }}
        >
          删除</MenuItem>
      </Menu>
    </Box>
  )
}